import { GET_CYCLES } from '../constants/action.types';

const initialState = {data: null};

export default function sampleReducer(state = initialState, action) {
    switch (action.type) {
        case GET_CYCLES:
            state.data = action.data;
            return state;
        default:
            return state;
    }
}



